.App
{
    height: 100vh;
    width: 100vw;
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.textInput
{
    background: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
}
.outputArea
{
    background: whitesmoke;
}
.inp
{
    height: 20px;
    width: 200px;
    border-radius: 5px;
    padding: 10px;
    background: whitesmoke;
    border: 0;
    outline: none;
}
.btn
{
    padding: 1rem 2rem;
    border: 0;
    color: white;
    font-family: sans-serif;
    background: mediumpurple;
    transition: background 100ms;
}
.btn:active
{
    background: #8161c0;
}
.frame
{
    height: 100%;
    width: 100%;
    border: 0;
}